* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.top-pane {
  background-color: rgb(42, 40, 54);
}
.pane {
  height: 50vh;
  display: flex;
}
.btn {
  background: none;
  border: none;
  color: white;
  outline: none;
  margin-left: 10px;
}
.editor-container {
  flex-grow: 1;
  flex-basis: 0;
  display: flex;
  flex-direction: column;
  padding: 0.5rem;
}
.editor-container.collpased {
  flex-grow: 0;
}
.editor-container.collpased .CodeMirror-scroll {
  position: absolute;
}
.editor-title {
  display: flex;
  justify-content: space-between;
  color: white;
  padding: 6px;
  font-size: 1.2rem;
}
.output {
  margin-top: 2px;
}
.CodeMirror {
  height: 100% !important;
}
.code-mirror-wrapper {
  flex-grow: 1;
  overflow: hidden;
}
